import axios from "axios";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

const BreadcrumbThree = () => {
  const { id } = useParams();
  const [centre, setCentre] = useState(null);

  useEffect(() => {
    const fetchCentre = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/${id}`
        );
        setCentre(response.data);
      } catch (error) {
        console.error("Failed to fetch centre:", error);
      }
    };

    fetchCentre();
  }, [id]);

  const [serDocApp, setSerDocApp] = useState();
  useEffect(() => {
    const fetchDetails = async () => {
      try {
        const response = await axios.get(
          `${process.env.REACT_APP_BACKEND_URL}/api/centres/stats?centreId=${id}`
        );

        setSerDocApp(response.data);
      } catch (error) {
        console.error("Failed to fetch ");
      }
    };
    fetchDetails();
  }, [id]);

  return (
    <section className="breadcrumb-three section-bg position-relative z-index-1 overflow-hidden">
      <img
        src="/assets/images/gradients/Bg.png"
        alt=""
        className="bg--gradient"
      />
      {/*
			<img
				src="assets/images/shapes/element-moon3.png"
				alt=""
				className="element one"
			/>

			<img
				src="assets/images/shapes/element-moon1.png"
				alt=""
				className="element three"
			/> */}

      <div className="container container-two">
        <div className="breadcrumb-three-content border-bottom border-color">
          <div className="breadcrumb-three-content__inner">
            <div className="breadcrumb-three-content__left">
              <div className="flx-between align-items-end gap-3">
                <div className="author-profile d-flex gap-2 flex-column">
                  <div className="author-profile__thumb flex-shrink-0">
                    <img
                      src={centre?.logo}
                      alt="logoImage"
                      style={{
                        width: "120px",
                        height: "120px",
                        borderRadius: "10%",
                        objectFit: "cover",
                      }}
                    />
                  </div>

                  <div className="author-profile__info">
                    <h5 className="author-profile__name mb-2">
                      {centre?.centreName}
                    </h5>

                    <span className="author-profile__membership font-14">
                      Member Since{" "}
                      <span id="member-since">
                        {centre?.createdAt
                          ? new Date(centre.createdAt).toLocaleString(
                              "default",
                              {
                                month: "long",
                                year: "numeric",
                              }
                            )
                          : ""}
                      </span>
                    </span>
                  </div>
                </div>

                {/* Stats start */}
                <div className="breadcrumb-three-content__right flex-shrink-0  d-flex align-items-center gap-4 gap-lg-5">
                  {/* <div className="author-rating">
                    <span className="author-rating__text text-heading fw-500 mb-2">
                      Rating
                    </span>

                    <div className="d-flex align-items-center gap-1">
                      <span id="rating-star">5</span>
                      <ul className="star-rating">
                        <li className="star-rating__item font-11">
                          <i className="fas fa-star" />
                        </li>
                        <li className="star-rating__item font-11">
                          <i className="fas fa-star" />
                        </li>
                        <li className="star-rating__item font-11">
                          <i className="fas fa-star" />
                        </li>
                        <li className="star-rating__item font-11">
                          <i className="fas fa-star" />
                        </li>
                        <li className="star-rating__item font-11">
                          <i className="fas fa-star" />
                        </li>
                      </ul>

                      <span className="star-rating__text text-body font-14">
                        {" "}
                        (<span id="review-count">116</span> reviews)
                      </span>
                    </div>
                  </div> */}

                  <div className="sales">
                    <span className="sales__text mb-1 text-heading fw-500">
                      Services
                    </span>

                    <h5 className="sales__amount mb-0">
                      {serDocApp?.totalServices}
                    </h5>
                  </div>

                  <div className="sales">
                    <span className="sales__text mb-1 text-heading fw-500">
                      Doctors
                    </span>

                    <h5 className="sales__amount mb-0">
                      {serDocApp?.totalDoctors}
                    </h5>
                  </div>

                  <div className="sales">
                    <span className="sales__text mb-1 text-heading fw-500">
                      Appointments
                    </span>

                    <h5 className="sales__amount mb-0">
                      {serDocApp?.totalBookings}
                    </h5>
                  </div>
                </div>
                {/* Stats end */}
              </div>
            </div>
          </div>

          {/* Tabs */}
          <ul
            className="nav tab-bordered nav-pills mt-4"
            id="pills-tabbs"
            role="tablist"
          >
            <li className="nav-item" role="presentation">
              <button
                className="nav-link active"
                id="pills-profile-tab"
                data-bs-toggle="pill"
                data-bs-target="#pills-profile"
                type="button"
                role="tab"
                aria-controls="pills-profile"
                aria-selected="true"
              >
                Profile
              </button>
            </li>

            <li className="nav-item" role="presentation">
              <button
                className="nav-link"
                id="pills-portfolio-tab"
                data-bs-toggle="pill"
                data-bs-target="#pills-portfolio"
                type="button"
                role="tab"
                aria-controls="pills-portfolio"
                aria-selected="false"
              >
                Services{" "}
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button
                className="nav-link"
                id="pills-package-tab"
                data-bs-toggle="pill"
                data-bs-target="#pills-package"
                type="button"
                role="tab"
                aria-controls="pills-package"
                aria-selected="false"
              >
                Health Checkup Packages{" "}
              </button>
            </li>

             <li className="nav-item" role="presentation">
              <button
                className="nav-link "
                id="pills-doctors-tab"
                data-bs-toggle="pill"
                data-bs-target="#pills-doctors"
                type="button"
                role="tab"
                aria-controls="pills-doctors"
                aria-selected="false"
              >
               Doctors
              </button>
            </li>
            <li className="nav-item" role="presentation">
              <button
                className="nav-link"
                id="pills-gallery-tab"
                data-bs-toggle="pill"
                data-bs-target="#pills-gallery"
                type="button"
                role="tab"
                aria-controls="pills-gallery"
                aria-selected="false"
              >
                Gallery{" "}
              </button>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default BreadcrumbThree;
